.secondary-navigation {
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 271px;
  z-index: 1;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.secondary-navigation__link:not(:last-child) {
  display: block;
  width: 40px;
  height: 40px;
  padding: 12px;
  border-radius: 20px;
  box-sizing: border-box;

  @media (min-width: @desktop-width) {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    padding: 17px;
  }
}

.secondary-navigation__link--phone {
  background-color: @base-orange;

  &:hover {
    background-color: @phone-orange-hover;
  }

  &:active {
    background-color: @phone-orange-hover;
  }

  &:active .secondary-navigation__icon {
    opacity: 0.3;
  }
}

.secondary-navigation__link--email {
  background-color: @base-yellow;

  &:hover {
    background-color: @base-orange;
  }

  &:active {
    background-color: @base-orange;
  }

  &:active .secondary-navigation__icon {
    opacity: 0.3;
  }
}

.secondary-navigation__link--login {
  display: block;
  margin: 0;
  width: 190px;
  min-height: 40px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 0;
  text-align: center;
  color: @secondary-text-dark;
  text-transform: uppercase;
  text-decoration: none;
  padding: 10px 40px 10px 42px;
  box-sizing: border-box;
  border-radius: 20px;
  background-color: @base-yellow;
}
