.table-list {
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 19px 18px 11px;
  background-color: @base-light;
  box-sizing: border-box;
  border-radius: 0 0 4px 4px;
  border: 1px solid @border-gray;
  border-top: none;

  @media (min-width: @tablet-width) {
    position: relative;
    // top: 0;
    // left: 100%;
    grid-column: 2/3;
    grid-row: 2/3;
    border-radius: 0;
    border: none;
    min-width: 206px;
    padding: 34px 0 33px 25px;
    box-shadow: 8px 8px 16px rgba(54, 80, 148, 0.18);
  }

  @media (min-width: @desktop-width) {
    max-width: 238px;
  }
}

.table-list__label {
  display: block;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 22px;
  letter-spacing: 0;
  text-align: left;
  color: @base-grey;
  cursor: pointer;
  margin-bottom: 4px;

  &:hover {
    color: @base-text-color;
  }

  &:active {
    opacity: 0.3;
  }

  &:focus {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0;
    margin-bottom: 10px;
  }
}

.table-list__checkbox:checked ~ .table-list__label {
  // background-color: @base-yellow;
  opacity: 0.3;
}
