  font-family: "Blogger Sans";
  src: url("../fonts/bloggersans-light-300.woff2") format("woff2"), url("../fonts/bloggersans-light-300.woff") format("woff");
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}
@font-face {
  font-family: "Blogger Sans";
  src: url("../fonts/bloggersans-lightitalic-300.woff2") format("woff2"), url("../fonts/bloggersans-lightitalic-300.woff") format("woff");
  font-style: italic;
  font-weight: 300;
  font-display: swap;
}
@font-face {
  font-family: "Blogger Sans";
  src: url("../fonts/bloggersans-400.woff2") format("woff2"), url("../fonts/bloggersans-400.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Blogger Sans";
  src: url("../fonts/bloggersans-italic-400.woff2") format("woff2"), url("../fonts/bloggersans-italic-400.woff") format("woff");
  font-style: italic;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Blogger Sans";
  src: url("../fonts/bloggersans-bold-700.woff2") format("woff2"), url("../fonts/bloggersans-bold-700.woff") format("woff");
  font-style: bold;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Blogger Sans";
  src: url("../fonts/bloggersans-bolditalic-700.woff2") format("woff2"), url("../fonts/bloggersans-bolditalic-700.woff") format("woff");
