.page__body {
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page__body {
    min-width: 768px;
  }
}
@media (min-width: 1440px) {
  .page__body {
    width: 100%;
  }
}
.header-login {
  justify-self: center;
  grid-column: 1/3;
}
@media (min-width: 768px) {
  .header-login {
    display: flex;
    grid-row: 1/2;
    grid-column: 3/4;
    justify-self: end;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .header-login {
    align-items: center;
    transform: translate(0, -4px);
  }
}
@media (min-width: 1440px) {
  .header-login__contacts {
    display: block;
    width: 50px;
    height: 50px;
    padding: 17px;
    border-radius: 25px;
    box-sizing: border-box;
  }
}
.header-login__contacts--phone:hover {
  background-color: #F26926;
}
.header-login__contacts--phone:active {
  background-color: #F26926;
}
.header-login__contacts--phone:active .header-login__contacts-icon {
  opacity: 0.3;
}
.header-login__contacts--phone:focus {
  background-color: #F26926;
}
.header-login__contacts--phone:focus .header-login__contacts-icon {
  opacity: 0.3;
}
@media (min-width: 1440px) {
  .header-login__contacts--phone {
    background-color: #ff8d30;
  }
}
.header-login__contacts--email:hover {
  background-color: #ff8d30;
}
.header-login__contacts--email:active {
  background-color: #ff8d30;
}
.header-login__contacts--email:active .header-login__contacts-icon {
  opacity: 0.3;
}
.header-login__contacts--email:focus {
  background-color: #ff8d30;
}
.header-login__contacts--email:focus .header-login__contacts-icon {
  opacity: 0.3;
}
@media (min-width: 1440px) {
  .header-login__contacts--email {
    background-color: #ffd74b;
  }
}
.header-login__link {
  grid-column: 1/3;
  justify-self: center;
  align-content: center;
  display: none;
  margin: 52px 0;
  padding: 0 12px;
  width: 270px;
  min-height: 50px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0;
  text-align: center;
  color: #161c35;
  text-transform: uppercase;
  text-decoration: none;
  box-sizing: border-box;
  border-radius: 25px;
  background-color: #ffd74b;
  word-break: break-word;
}
.header-login__link:hover {
  background-color: #ffbd30;
}
.header-login__link:active {
  background-color: #ffbd30;
  color: rgba(23, 29, 54, 0.3);
}
.header-login__link:focus {
  background-color: #ffbd30;
  color: rgba(23, 29, 54, 0.3);
}
@media (min-width: 768px) {
  .header-login__link {
    display: grid;
    grid-row: 1/2;
    grid-column: 3/4;
    margin: 0;
    width: 188px;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
  }
}
.header-login__link--active {
  display: grid;
}
@media (min-width: 768px) {
  .header-login__link--active {
    font-weight: 400;
  }
}
@media (max-width: 1440px) {
  .header-login__contacts-icon {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-header__burger-menu-icon {
    display: none;
  }
}
.main-header {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  min-height: 73px;
  z-index: 5;
}
@media (min-width: 768px) {
  .main-header {
    min-height: 110px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    z-index: 5;
  }
}
.main-header__top {
  position: fixed;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  min-height: 73px;
  padding: 19px 21px 35px 25px;
  background-color: #161c35;
  box-sizing: border-box;
  width: 320px;
  z-index: 5;
}
@media (min-width: 768px) {
  .main-header__top {
    grid-template-columns: 1fr 1fr 1fr;
    padding: 40px 40px;
    min-height: 110px;
    background-color: #182044;
    align-items: center;
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .main-header__top {
    position: static;
    grid-template-columns: 0.9fr 3fr 1.2fr;
    padding: 42px 60px 0;
    height: auto;
    background-color: #192144;
    width: 1440px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .main-header__top--main-page {
    background-color: #161c35;
  }
}
.main-header__top--scroll {
  background: #ffffff;
}
.main-header__top--no-scroll {
  background: #161c35;
}
.main-header__top--nojs {
  position: relative;
}
@media (max-width: 1440px) {
  .main-header__top--nojs {
    background: #ffffff;
  }
}
@media (min-width: 1440px) {
  .main-header__top--fixed {
    position: fixed;
    background: #ffffff;
    left: 50%;
    transform: translateX(-50%);
  }
}
.main-header__burger-menu-active {
  background-color: #ffffff;
}
.main-header__check-scrol {
  opacity: 1;
}
.main-header__check-menu-open {
  opacity: 1;
}
.main-header__logo {
  display: block;
  align-self: center;
  grid-column: 1/2;
  grid-row: 1/2;
  transform: translate(2px, 0);
}
@media (min-width: 768px) {
  .main-header__logo {
    position: relative;
    margin-left: 58px;
    z-index: 1;
    transform: translate(-2px, 2px);
  }
}
@media (min-width: 1440px) {
  .main-header__logo {
    position: relative;
    margin-left: 65px;
    transform: translate(2px, 0);
  }
}
.main-header__logo--secondary {
  transform: translate(1px, 1px);
}
@media (min-width: 768px) {
  .main-header__logo--secondary {
    position: relative;
    margin-left: 58px;
    z-index: 1;
    transform: translate(-1px, 2px);
  }
}
@media (min-width: 1440px) {
  .main-header__logo--secondary {
    position: relative;
    margin-left: 65px;
    transform: translate(2px, -2px);
  }
}
.main-header__logo--catalog {
  transform: translate(-1px, 0);
}
@media (min-width: 768px) {
  .main-header__logo--catalog {
    transform: translate(-1px, 2px);
  }
}
@media (min-width: 1440px) {
  .main-header__logo--catalog {
    transform: translate(2px, -2px);
  }
}
@media (max-width: 1440px) {
  .main-header__top--nojs .main-header__logo-image--light {
    display: none;
  }
}
@media (max-width: 1440px) {
  .main-header__top--nojs .main-header__burger-menu {
    display: none;
  }
}
@media (max-width: 1440px) {
  .main-header__top--nojs .visually-hidden {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    border: 0;
    padding: 0;
    white-space: nowrap;
    /* stylelint-disable-next-line */
    -webkit-clip-path: none;
    clip-path: none;
    clip: auto;
    overflow: visible;
  }
}
@media (max-width: 768px) {
  .main-header__top--nojs .social {
    margin: 43px 0 17px;
    width: 110px;
    text-align: center;
    justify-self: center;
  }
}
@media (min-width: 768px) and (max-width: 1440px) {
  .main-header__top--nojs .social {
    margin: 129px 0 55px;
    width: 160px;
    padding-left: 83px;
  }
}
@media (max-width: 1440px) {
  .main-header__top--nojs .main-menu__login-icon {
    display: none;
  }
}
@media (max-width: 768px) {
  .main-header__top--nojs .main-menu__login {
    display: grid;
  }
}
@media (min-width: 768px) {
  .main-header__top--nojs .navigation-user {
    padding-left: 47px;
  }
}
@media (min-width: 768px) {
  .main-header__top::before {
    position: absolute;
    grid-column: 1/2;
    grid-row: 1/2;
    z-index: 1;
    content: "";
    width: 155px;
    height: 42px;
    background: url("../images/color-logo-tablet.svg") no-repeat;
  }
}
@media (min-width: 1440px) {
  .main-header__top::before {
    position: relative;
    grid-column: 1/2;
    grid-row: 1/2;
    top: -6px;
    left: -3px;
    z-index: 0;
    content: "";
    width: 187px;
    height: 50px;
    background: url("../images/color-logo-desktop.svg") no-repeat;
  }
}
.main-header__burger-menu {
  grid-column: 2/3;
  grid-row: 1/2;
  justify-self: end;
  background: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .main-header__burger-menu {
    justify-self: center;
  }
}
.main-header__top-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  min-width: 270px;
  min-height: 15px;
}
.main-header__logo-image {
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-header__logo-image {
    width: 111px;
    height: 21px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo-image {
    width: 133px;
    height: 25px;
  }
}
.main-header--secondary {
  position: relative;
  border-radius: 0 0 30px 30px;
}
.main-header__border {
  position: absolute;
  width: 214px;
  height: 5px;
  border-radius: 2.5px;
  bottom: 0;
  left: 25px;
  transform: translateY(50%);
  background-color: #ff8d30;
}
@media (min-width: 768px) {
  .main-header__border {
    height: 8px;
  }
}
.main-header-bottom {
  position: relative;
  width: 100%;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-header-bottom {
    top: 0;
  }
}
@media (min-width: 1440px) {
  .main-header-bottom {
    padding-top: 0;
    background-color: #192144;
    border-radius: 0 0 60px 60px;
    z-index: 3;
  }
}
.main-header-bottom__main {
  padding: 18px 0 10px 25px;
  box-sizing: border-box;
  background-color: #161c35;
  border-radius: 0 0 20px 20px;
}
@media (min-width: 768px) {
  .main-header-bottom__main {
    padding: 108px 70px 66px 75px;
    background: #182044;
    border-radius: 0 0 60px 60px;
    top: 100%;
  }
}
@media (min-width: 1440px) {
  .main-header-bottom__main {
    background-color: #192144;
    padding: 110px 120px 66px 120px;
    border-radius: 0 0 60px 60px;
    width: 1440px;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .main-header-bottom__main--catalog {
    padding: 106px 70px 68px 75px;
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .main-header-bottom__main--catalog {
    padding: 106px 118px 68px 125px;
    display: flex;
    justify-content: space-between;
  }
}
.main-header-bottom__link {
  display: none;
}
@media (min-width: 768px) {
  .main-header-bottom__link {
    display: block;
    font-family: "Blogger Sans", "Arial", sans-serif;
    font-size: 20px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0;
    text-transform: uppercase;
    text-decoration: none;
    color: #ffffff;
    max-width: 250px;
    align-self: center;
    padding-top: 6px;
    justify-self: end;
  }
  .main-header-bottom__link:hover {
    opacity: 0.7;
  }
  .main-header-bottom__link:active {
    opacity: 0.3;
  }
  .main-header-bottom__link:focus {
    opacity: 0.3;
  }
}
.main-header-bottom__icon {
  margin: 0 3px 0 25px;
}
.main-header-bottom__name {
  display: inline-block;
  margin: 0;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 700;
  font-style: normal;
  line-height: 36px;
  letter-spacing: 0;
  text-align: left;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-header-bottom__name {
    max-width: 450px;
    word-break: break-all;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 1440px) {
  .main-header-bottom__name {
    max-width: 500px;
  }
}
.main-header-bottom__name::after {
  content: "";
  width: auto;
  height: 5px;
  background-color: #ff8d30;
  border-radius: 2px;
  display: block;
  position: relative;
  top: 10px;
  transform: translateY(50%);
}
@media (min-width: 768px) {
  .main-header-bottom__name::after {
    top: 66px;
  }
}
.first-screen {
  padding-top: 20px;
  margin-bottom: -100px;
  position: relative;
  background: url("../images/first-screen-bg-mobile.png") no-repeat top 10px center, #161c35;
}
@media (min-resolution: 2dppx) {
  .first-screen {
    background: url("../images/first-screen-bg-mobile@2x.png") no-repeat top 10px center, #161c35;
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .first-screen {
    padding-top: 45px;
    min-height: 833px;
    margin-bottom: -140px;
    background: url("../images/first-screen-tablet-bg.png") no-repeat bottom 140px center, #182044;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: repeat(3, auto);
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .first-screen {
    background: url("../images/first-screen-tablet-bg@2x.png") no-repeat bottom 140px center, #182044;
    background-size: 768px auto;
  }
}
@media (min-width: 1440px) {
  .first-screen {
    position: relative;
    z-index: 4;
    grid-template-columns: 1fr 1fr 358px;
    min-height: 400px;
    padding: 0;
    background: url("../images/first-screen-bg-desktop.png") no-repeat bottom 0 center, #161c35;
    width: 1440px;
    margin: 0 auto;
    box-sizing: border-box;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .first-screen {
    background: url("../images/first-screen-bg-desktop@2x.png") no-repeat bottom 0 center;
    background-size: 1440px auto;
  }
}
.first-screen__header {
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 36px;
  font-weight: 700;
  line-height: 40px;
  letter-spacing: 0;
  text-align: center;
  color: #ffffff;
  margin: -3px 0 0;
  padding: 0 35px;
}
@media (min-width: 768px) {
  .first-screen__header {
    display: block;
    grid-column: 1/3;
    grid-row: 1/2;
    width: 400px;
    min-height: 210px;
    font-size: 60px;
    line-height: 70px;
    margin: 0 auto 20px;
  }
}
@media (min-width: 1440px) {
  .first-screen__header {
    grid-column: 1/2;
    padding: 95px 0 0 110px;
    text-align: left;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .first-screen__header ~ picture {
    grid-column: 2/3;
    grid-row: 2/3;
    align-self: end;
    justify-self: start;
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .first-screen__header ~ picture {
    grid-column: 2/3;
    grid-row: 1/2;
    align-self: start;
    margin-top: -6px;
  }
}
.first-screen__companions {
  position: relative;
  display: block;
  margin: 12px 0 0 80px;
}
@media (min-width: 768px) {
  .first-screen__companions {
    grid-column: 2/3;
    grid-row: 2/3;
    width: 372px;
    height: 451px;
    margin: 20px 0 0 5px;
    align-self: end;
    justify-self: end;
  }
}
@media (min-width: 1440px) {
  .first-screen__companions {
    position: relative;
    display: block;
    width: 432px;
    height: 640px;
    justify-self: end;
    margin: 8px 0 0 61px;
  }
}
