.tariffs__price {
  grid-column: 1/3;
  display: grid;
  grid-template-columns: 1fr 44px;
  grid-auto-rows: minmax(65px, auto);
}
@media (min-width: 768px) {
  .tariffs__price {
    grid-column: 1/4;
    grid-template-columns: 68px 1fr 200px;
    position: relative;
    margin-top: 5px;
  }
}
@media (min-width: 1440px) {
  .tariffs__price {
    grid-template-columns: 68px 1fr 446px;
  }
}
@media (min-width: 768px) {
  .tariffs__price::after {
    content: "";
    position: absolute;
