@media (min-width: 1440px) {
  .bg-wrapper--dark-active {
    border-radius: 0;
  }
}
.bg-wrapper--first-screen {
  position: relative;
  z-index: 4;
  border-radius: 0 0 60px 60px;
  background: #161c35;
}
.bg-wrapper__svg {
  display: none;
}
@media (min-width: 1440px) {
  .bg-wrapper__svg {
    display: block;
    position: absolute;
    width: 100%;
    height: 642px;
    z-index: 4;
