/* FOOTER */

.main-footer {
  position: relative;

  @media (min-width: @desktop-width) {
    border-radius: 60px 60px 0 0;
    background-color: @base-light;
  }
}

.main-footer__main {
  display: grid;
  background-color: @base-light;
  border-radius: 30px 30px 0 0;
  text-align: center;
  padding: 37px 25px 19px;

  @media (min-width: @tablet-width) {
    grid-template-columns: 24% 52% 24%;
    align-items: center;
    padding: 46px 40px 32px;
    border-radius: 60px 60px 0 0;
  }

  @media (min-width: @desktop-width) {
    padding: 47px 60px 43px;
    width: 1440px;
    margin: 0 auto;
    box-sizing: border-box;
  }
}

.main-footer__logo {
  margin: 0 0 35px;
  justify-self: center;

  @media (min-width: @tablet-width) {
    position: relative;
    justify-self: end;
    // margin-right: 10px;
    margin: 0;
    width: 107px;
    height: 17px;
  }

  @media (min-width: @desktop-width) {
    justify-self: start;
    margin-left: 71px;
  }
}

.main-footer__logo-icon {
  @media (min-width: @tablet-width) {
    width: 107px;
    height: 17px;
  }

  @media (min-width: @desktop-width) {
    width: 128px;
    height: 20px;
    transform: translate(-2px, -2px);
  }
}

.main-footer__main::before {
  @media (min-width: @tablet-width) {
    position: absolute;
    z-index: 0;
    content: "";
    width: 155px;
    height: 42px;
    background: url("../images/color-logo-tablet.svg");
  }

  @media (min-width: @desktop-width) {
    position: absolute;
    z-index: 0;
    content: "";
    width: 187px;
    height: 50px;
    background: url("../images/color-logo-desktop.svg") no-repeat left center;
  }
}

.main-footer__logo-link {
  margin: 5px 0 0;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 16px;
  letter-spacing: 0;
  text-align: left;
  color: @base-grey;
  text-decoration: none;
  padding-top: 13px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 270px;
  min-height: 33px;
  border-top: 1px solid @footer-border-color;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }

  &:focus {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 20px;
    // margin-right: 23px;
    border: 0;
    padding: 0;
    justify-content: end;
    width: auto;
    margin-top: 0;
    transform: translate(0, -2px);
  }

  @media (min-width: @desktop-width) {
    margin: -5px 0 0 3px;
  }
}

.main-footer__logo-left {
  margin-left: 24px;

  @media (min-width: @desktop-width) {
    margin-top: 5px;
  }
}
