.main-header {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  min-height: 73px;
  z-index: 5;

  @media (min-width: @tablet-width) {
    min-height: 110px;
  }

  @media (min-width: @desktop-width) {
    z-index: 5;
  }
  // background: url("../images/first-screen-bg-mobile.png");
}

.main-header__top {
  position: fixed;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  min-height: 73px;
  padding: 19px 21px 35px 25px;
  background-color: @secondary-text-dark;
  box-sizing: border-box;
  width: 320px;
  z-index: 5;

  @media (min-width: @tablet-width) {
    grid-template-columns: 1fr 1fr 1fr;
    padding: 40px 40px;
    min-height: 110px;
    background-color: @tablet-dark-bg;
    align-items: center;
    width: 768px;
  }

  @media (min-width: @desktop-width) {
    position: static;
    grid-template-columns: 0.9fr 3fr 1.2fr;
    padding: 42px 60px 0;
    height: auto;
    background-color: @main-background-dark;
    width: 1440px;
    margin: 0 auto;
  }
}

.main-header__top--main-page {
  @media (min-width: @desktop-width) {
    background-color: @secondary-text-dark;
  }
}

.main-header__top--scroll {
  background: @base-light;
}

.main-header__top--no-scroll {
  background: @secondary-text-dark;
}

.main-header__top--nojs {
  position: relative;

  @media (max-width: @desktop-width) {
    background: @base-light;
  }
}

.main-header__top--fixed {
  @media (min-width: @desktop-width) {
    position: fixed;
    // padding: 23px 0;
    background: @base-light;
    left: 50%;
    transform: translateX(-50%);
  }
}


.main-header__burger-menu-active {
  background-color: @base-light;
}

.main-header__check-scrol {
  opacity: 1;
}

.main-header__check-menu-open {
  opacity: 1;
}

.main-header__logo {
  display: block;
  align-self: center;
  grid-column: 1/2;
  grid-row: 1/2;
  transform: translate(2px, 0);

  @media (min-width: @tablet-width) {
    position: relative;
    margin-left: 58px;
    z-index: 1;
    transform: translate(-2px, 2px);
  }

  @media (min-width: @desktop-width) {
    position: relative;
    margin-left: 65px;
    transform: translate(2px, 0);
  }
}

.main-header__logo--secondary {
  transform: translate(1px, 1px);

  @media (min-width: @tablet-width) {
    position: relative;
    margin-left: 58px;
    z-index: 1;
    transform: translate(-1px, 2px);
  }

  @media (min-width: @desktop-width) {
    position: relative;
    margin-left: 65px;
    transform: translate(2px, -2px);
  }
}

.main-header__logo--catalog {
  transform: translate(-1px, 0);

  @media (min-width: @tablet-width) {
    transform: translate(-1px, 2px);
  }

  @media (min-width: @desktop-width) {
    transform: translate(2px, -2px);
  }
}

.main-header__top--nojs .main-header__logo-image--light {
  @media (max-width: @desktop-width) {
    display: none;
  }
}

.main-header__top--nojs .main-header__burger-menu {
  @media (max-width: @desktop-width) {
    display: none;
  }
}

.main-header__top--nojs .visually-hidden {
  @media (max-width: @desktop-width) {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    border: 0;
    padding: 0;
    white-space: nowrap;
    /* stylelint-disable-next-line */
    -webkit-clip-path: none;
    clip-path: none;
    clip: auto;
    overflow: visible;
  }
}

.main-header__top--nojs .social {
  @media (max-width: @tablet-width) {
    margin: 43px 0 17px;
    width: 110px;
    text-align: center;
    justify-self: center;
  }

  @media (min-width: @tablet-width) and (max-width: @desktop-width) {
    margin: 129px 0 55px;
    width: 160px;
    padding-left: 83px;
  }
}

.main-header__top--nojs .main-menu__login-icon {
  @media (max-width: @desktop-width) {
    display: none;
  }
}

.main-header__top--nojs .main-menu__login {
  @media (max-width: @tablet-width) {
    display: grid;
  }
}

.main-header__top--nojs .navigation-user {
  @media (min-width: @tablet-width) {
    padding-left: 47px;
  }
}

.main-header__top::before {
  @media (min-width: @tablet-width) {
    position: absolute;
    grid-column: 1/2;
    grid-row: 1/2;
    // transform: translate(-38%, -25%);
    // top: 40px;
    // left: 40px;
    z-index: 1;
    content: "";
    width: 155px;
    height: 42px;
    background: url("../images/color-logo-tablet.svg") no-repeat;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    grid-column: 1/2;
    grid-row: 1/2;
    // transform: translate(-28%, -25%);
    top: -6px;
    left: -3px;
    z-index: 0;
    content: "";
    width: 187px;
    height: 50px;
    background: url("../images/color-logo-desktop.svg") no-repeat;
  }
}

.main-header__burger-menu {
  grid-column: 2/3;
  grid-row: 1/2;
  // display: block;
  justify-self: end;
  background: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;

  @media (min-width: @tablet-width) {
    justify-self: center;
  }
}

.main-header__top-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  min-width: 270px;
  min-height: 15px;
}

.main-header__logo-image {
  position: relative;
  z-index: 1;

  @media (min-width: @tablet-width) {
    width: 111px;
    height: 21px;
  }

  @media (min-width: @desktop-width) {
    width: 133px;
    height: 25px;
  }
}

