.main-header__border {
  position: absolute;
  width: 214px;
  height: 5px;
  border-radius: 2.5px;
  bottom: 0;
  left: 25px;
  transform: translateY(50%);
  background-color: @base-orange;

  @media (min-width: @tablet-width) {
    height: 8px;
  }
}
